/* About me Imgage */
.img {
  height: 350px !important;
}

/*  */
.custom-toggler.navbar-toggler {
  border-color: none !important;
  border: none
}

/* Setting the stroke to green using rgb values (0, 128, 0) */

.custom-toggler .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgb(109, 131, 242)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E") !important;
}


.navbar-toggler {
  color: #0d6efd;
}

.navbar-toggler:focus {
  text-decoration: none !important;
  outline: none !important;
  box-shadow: none !important;
}

.letter-spcaing {
  letter-spacing: 2px;

}

.blue-image {
  width: 40%;
  position: absolute;
  top: 75px;
  right: 0px;
  z-index: -1
}

.perfect-blue {
  background-color: white;
}

/* For the placeholder */

.form-control::placeholder {
  color: black;
  opacity: 0.6;
}

.aboutme-fs {
  font-size: 17px;
}

/* Dark mode button */
.form-switch .form-check-input {
  width: 2.4em;
  /* border-color: rgb(109, 131, 242);
  border-width: 2px; */
}

.form-check-input {
  height: 20px;
}


/* Download Resume Button */

.custom-btn {
  width: 190px;
  height: 50px;
  color: #fff;
  border-radius: 5px;
  /* padding: 10px 25px; */
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  /* background: transparent; */
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  /* box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1); */
  outline: none;
}

.btn-design {
  border: none;
  color: #000;
}

.btn-design:after {
  position: absolute;
  content: "";
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  direction: rtl;
  z-index: -1;
  box-shadow:
    -7px -7px 20px 0px #fff9,
    -4px -4px 5px 0px #fff9,
    7px 7px 20px 0px #0002,
    4px 4px 5px 0px #0001;
  transition: all 0.3s ease;
}

.btn-design:hover {
  color: #000;
}

.btn-design:hover:after {
  left: auto;
  right: 0;
  width: 100%;
}

.btn-design:active {
  top: 2px;
}

