/* Media Queries */
/* For I-Phones */

@media only screen and (max-width: 384px) {
  .query-textcenter-md {
    display: block !important;
  }

  /* Margins from top for both blocks */
  .margin-top {
    margin-top: 6rem;
  }

  .query-textcenter {
    text-align: center;
  }

  .blue-image {
    width: 40%;
    position: absolute;
    top: 56px;
    right: 0px;
    z-index: -1
  }

  .img {
    height: 300px !important;
  }

  /* Contact full width.. NEM */
  .full-widht {
    width: 100% !important;
  }

  /* Portfolio font size */
  .port {
    font-size: 20px !important;
  }

  .folio {
    font-size: 20px !important;
  }

  .navbar-items {
    font-size: 16px !important;
  }

  .fontsize-arslan {
    font-size: 32px !important;
  }

  .fs_creative {
    font-size: 17px !important;
  }

  .json-text {
    font-size: 14.5px !important;
  }

  .json-text-ntas-smallscreen {
    font-size: 13.5px !important;
  }

  .json-padding {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .contact-npem {
    font-size: 17px !important;
  }

  .json-margin {
    margin-left: none !important;
  }
}

